@import './public/variables.scss';

.Home {
  width: 100%;
  height: auto;
  flex-shrink: 0;

  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-content: center;
  align-items: center;

  padding: 0 $mobile_padding;

  @include breakpoint(tablet){
    padding: 0 $tablet_padding;
  }

  @include breakpoint(laptop){
    width: $widthSize-laptop;

    padding: 0;
  }

  @include breakpoint(desktop){
    width: $widthSize-desktop;
  }

  @include breakpoint(large_desktop){
    width: $widthSize-largeDesktop;
  }

  /***Top***/

  &-top {
    width: 100%;
    height: auto;
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-content: center;
    align-items: center;

    margin-bottom: 320px;

    flex-shrink: 0;
  }

  &-img {
    position: relative;
    width: 287px;
    height: 392px;
    display: flex;
    flex-shrink: 0;

    //background-image: url('/img/imgPortada.jpg');
    background-size: cover;
    background-position: center;

    margin-bottom: 33px;

    @include breakpoint(tablet){
      width: 314px;
      height: 427px;
    }

    @include breakpoint(large_desktop){
      width: 466px;
      height: 634px;
    }

    >svg {
      width: 14px;
      height: 236px;
      position: absolute;
      bottom: 0;
      left: -20px;

      >path {
        fill: $blackColor;
      }

      >line {
        stroke: $blackColor;
        stroke-width: 2;
      }
    }
  }

  &-topInfo {
    width: 100%;
    height: auto;

    @include breakpoint(tablet){
      width: 355px;
    }

    @include breakpoint(large_desktop) {
      width: 426px;
    }

    >h3 {
      width: 100%;
      text-align: center;

      font-family: $titleFont;
      font-style: normal;
      font-weight: 900;
      font-size: 28px;
      line-height: 120%;

      color: $blackColor;

      margin-bottom: 20px;

      @include breakpoint(tablet){
        font-size: 36px;
      }

      @include breakpoint(large_desktop) {
        font-size: 42px;
      }
    }

    >h4 {
      width: 100%;

      font-family: $textFont;
      font-style: normal;
      font-weight: normal;
      font-size: 18px;
      line-height: 120%;
      text-align: center;

      color: $blackColor;

      @include breakpoint(tablet){
        font-size: 20px;
      }

      @include breakpoint(large_desktop) {
        font-size: 26px;
      }
    }
  }

  &-strongColor {
    color: $mainColor;
  }

  /***WhatIDo***/

  &-whatIDo {
    width: 100%;
    height: auto;

    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: flex-start;
    align-content: flex-start;
    align-items: flex-start;

    flex-shrink: 0;

    margin-bottom: 125px;

    @include breakpoint(desktop){
      flex-direction: row;
      justify-content: space-between;
    }

    @include breakpoint(large_desktop){
      margin-bottom: 280px;
    }

  }

  &-whatLeft {
    width: auto;
    height: auto;

    margin-bottom: 15px;

    @include breakpoint(desktop){
      width: 160px;
    }

    @include breakpoint(large_desktop){
      width: 210px;
    }

    >h3 {
      width: 100%;
      height: auto;

      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;

      font-family: $titleFont;
      font-style: normal;
      font-weight: bold;
      font-size: 30px;
      line-height: 120%;

      color: $blackColor;

      text-align: right;

      @include breakpoint(large_desktop) {
        font-size: 36px;
      }
    }
  }

  &-whatRight {
    width: 100%;
    height: auto;

    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-content: center;
    align-items: flex-start;

    @include breakpoint(desktop){
      width: 610px;
    }

    @include breakpoint(large_desktop){
      width: 720px;
    }
  }

  &-profile {
    width: 100%;
    height: auto;

    @include breakpoint(laptop){
      width: 50%;
    }
  }

  &-other {
    width: 100%;
    height: auto;

    @include breakpoint(laptop){
      width: 50%;
    }
  }

  &-whatTitle {
    color: $blackColor;

    font-family: $titleFont;
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 120%;

    margin-bottom: 16px;
    margin-top: 16px;

    @include breakpoint(large_desktop) {
      margin-bottom: 20px;
      font-size: 26px;
    }
  }

  &-whatText {
    color: $blackColor;

    font-family: $textFont;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 120%;

    margin-bottom: 3px;


    @include breakpoint(large_desktop) {
      margin-bottom: 5px;
      font-size: 22px;
    }
  }

  /***Recent***/

  &-recentWork {
    width: 100%;
    height: auto;

    margin-bottom: 130px;

    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-content: center;
    align-items: flex-start;

    @include breakpoint(desktop){
      flex-direction: row;
      justify-content: space-between;
    }

    @include breakpoint(desktop){
      margin-bottom: 280px;
    }
  }

  &-leftRecent {
    width: auto;
    height: auto;

    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;

    font-family: $titleFont;
    font-style: normal;
    font-weight: bold;
    font-size: 30px;
    line-height: 120%;

    color: $blackColor;

    text-align: left;

    margin-bottom: 30px;

    @include breakpoint(desktop){
      width: 160px;
      text-align: right;
    }

    @include breakpoint(large_desktop){
      width: 203px;
      font-size: 36px;
    }

  }

  &-rightRecent {
    width: 100%;
    height: auto;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-content: center;
    align-items: flex-end;

    
    @include breakpoint(desktop){
      width: 662px;
    }

    @include breakpoint(large_desktop){
      width: 993px;
    }
  }

  &-containerRecent {
    width: 100%;
    height: auto;

    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: flex-start;
    align-content: center;
    align-items: flex-start;

    @include breakpoint(laptop){
      flex-direction: row;
      flex-wrap: wrap;
      align-items: flex-end;
      align-content: center;
      align-items: flex-start;
  }

    margin-bottom: 24px;
  }

  /***Contact***/

  &-contact {
    width: 100%;
    height: auto;

    margin-bottom: 145px;

    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-content: center;
    align-items: flex-start;

    @include breakpoint(desktop){
      flex-direction: row;
    }
  }

  &-leftContact {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-content: flex-end;
    align-items: flex-end;

    @include breakpoint(desktop){
      width: 516px;
    }

    @include breakpoint(large_desktop){
      width:650px;
    }

    >h5 {
      font-family: $titleFont;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 120%;
      display: flex;
      align-items: center;
      text-align: right;

      margin-bottom: 5px;

      /* Black */

      color: $blackColor;

      @include breakpoint(desktop){
        margin-right: 25px;
        font-size: 22px;
      }

      >strong {
        color: $mainColor;

        margin-left: 6px;
        margin-right: 6px;
      }
    }

    >h3 {
      font-family: $textFont;
      font-style: normal;
      font-weight: 900;
      font-size: 20px;
      line-height: 120%;
      display: flex;
      align-items: flex-end;
      text-align: right;

      /* Black */

      color: $blackColor;

      @include breakpoint(desktop){
        margin-right: 25px;
        font-size: 28px;
      }

    }
  }

  &-contactImg {
    width: 100%;

    margin-bottom: 66px;

    flex-shrink: 0;

    @include breakpoint(laptop) {
      margin-bottom: 50px;
    }

    @include breakpoint(large_desktop) {
      margin-bottom: 130px;
    }

  }

  &-rightContact {
    margin-top: 29px;

    width: 100%;
    height: auto;

    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-content: flex-end;
    align-items: center;

    @include breakpoint(desktop){
      width: 314px;

      margin-top: 160px;
    }

    @include breakpoint(large_desktop) {
      width: 345px;

      margin-top: 320px;
    }
  }

  &-containerContact {
    width: 100%;
    height: 283px;

    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-content: flex-start;
    align-content: flex-start;

    text-align: left;

    flex-shrink: 0;

    background-color: $blackColor;

    padding: 36px;

    >h3 {
      font-family: $titleFont;
      font-style: normal;
      font-weight: 900;
      font-size: 48px;
      line-height: 56px;
      display: flex;
      align-items: flex-end;

      /* White */

      color: $whiteColor;
    }

    >h5 {
      font-family: $titleFont;
      font-style: normal;
      font-weight: normal;
      font-size: 32px;
      line-height: 32px;
      display: flex;
      align-items: flex-end;

      /* White */

      color: $whiteColor;

      margin-bottom: 36px;
    }
  }

  &-cv {
    width: 100%;
    height: 48px;

    background-color: $whiteColor;

    font-family: $textFont;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 19px;
    display: flex;
    align-items: center;
    text-align: center;

    /* Black */

    color: $blackColor;

    border: none;

    display: flex;
    justify-content: center;
    flex-shrink: 0;

    transition: ease-in-out all .2s;

    &:hover {
      background-color: $mainColor;

      color: $whiteColor;
    }
  }

  /***Footer***/
  footer {
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-content: center;
    align-items: center;
    height: auto;

    margin-bottom: 90px;

    >h4 {
      font-family: $textFont;
      font-style: normal;
      font-weight: normal;
      font-size: 12px;
      line-height: 120%;
      display: flex;
      align-items: flex-end;
      text-align: center;

      /* Black */

      color: $blackColor;
    }

    @include breakpoint(tablet) {
      margin-bottom: $paddingSize;
    }
  }

}