@import './public/variables.scss';

.ProjectCard{
    width: 100%;
    height: auto;

    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-content: center;
    align-items: flex-start;

    cursor: pointer;

    position: relative;

    text-decoration: none;

    @include breakpoint(laptop){
        width: 50%;
    }

    @include breakpoint(desktop){
        width: $sizeProjectCard;
    }

    &:hover{
        background-size: cover;
    }

    &-img{
        width: 100%;
    }

    &-title{
        width: 100%;
        height: 100%;

        padding: $card_paddingSize;

        color: $whiteColor;
        position: absolute;

        display: flex;
        flex-direction: column;
        justify-content: flex-end;

        flex-shrink: 0;

        >h3{
            width: auto;
            height: auto;
            font-family: $titleFont;
            font-style: normal;
            font-weight: bold;
            font-size: 36px;
            line-height: 100%;
        }

    }

    &-data{

        color: $whiteColor;

        padding: $card_paddingSize;
        position: absolute;

        width: 100%;
        height: 100%;

        display: flex;
        flex-shrink: 0;

        flex-direction: column;

        justify-content: space-between;

        background-color: rgba($color: $blackColor, $alpha: 0.8);

        >h3{
            font-family: $titleFont;
            font-style: normal;
            font-weight: bold;
            font-size: 36px;
            line-height: 120%;
        }

        >p{
            font-family: $titleFont;
            font-style: normal;
            font-weight: normal;
            font-size: 25px;
            line-height: 120%;

            @include breakpoint(laptop){
                font-size: 20px;
            }

            @include breakpoint(desktop){
                font-size: 25px;
            }
        }

        >svg{
            width: 28px;

            >path{
                stroke: $whiteColor;
                stroke-width: 2;
                stroke-linecap: round;
            }
        }
    }
}